import React, { useContext } from 'react';

import { Card, CardHeader, CardBody } from 'reactstrap';
import { Bell, Settings, XCircle, PlayCircle, AlertCircle,CheckCircle } from 'react-feather';
import Chart from 'react-apexcharts';
import { InsightsOverviewContext } from 'contexts/InsightsOverviewContext'


export default function SentimentDistribution() {
    const {sentimentDistribution} = useContext(InsightsOverviewContext);
    const strongNegative = sentimentDistribution.strongNegative
    const negative = sentimentDistribution['negative']
    const neutral = sentimentDistribution['neutral']
    const positive = sentimentDistribution['positive']
    const strongPositive = sentimentDistribution['strongPositive']

    const options = {
        labels: ['Strong Negative', 'Negative', 'Neutral','Positive', 'Strong Positive']
    };
    const series = [strongNegative,negative,neutral,positive,strongPositive];
    return (
        <>
            <Card className="card-box">
                <CardHeader >
                    <div className="p-0">
                        <h6 className="font-weight-bold font-size-lg mb-1 text-black">
                            Overall Sentiment Distribution
                        </h6>
                        <p className="text-black-50 font-size-md mb-0">
                            View Overall Sentiment Distribution across different level
                        </p>
                    </div>

                </CardHeader>
                <CardBody>
                    <div className="d-flex flex-row flex-wrap justify-content-center">
                        <div className="py-4 px-5 d-flex align-items-center">
                            <XCircle className="d-50 text-danger mr-3" />
                            <div className="text-center">
                                <span className="d-block text-danger">Strong Negative</span>
                                <span className="font-weight-bold font-size-xl text-danger">
                                    {strongNegative}
                                </span>
                            </div>
                        </div>
                        <div className="px-5 d-flex align-items-center">
                            <AlertCircle className="d-50 text-warning mr-3" />
                            <div className="text-center">
                                <span className="d-block text-warning">Negative</span>
                                <span className="font-weight-bold font-size-xl text-warning">
                                {negative}
                                </span>
                            </div>
                        </div>
                        <div className="py-4 px-5 d-flex align-items-center">
                            <CheckCircle className="d-50 text-success mr-3" />
                            <div className="text-center">
                                <span className="d-block text-sucess">Strong Positive</span>
                                <span className="font-weight-bold font-size-xl text-sucess">
                                {strongPositive}
                                </span>
                            </div>
                        </div>
                        <div className="px-5 d-flex align-items-center">
                            <PlayCircle className="d-50 text-info mr-3" />
                            <div className="text-center">
                                <span className="d-block text-info">Positive</span>
                                <span className="font-weight-bold font-size-xl text-info">
                                    {positive}
                                </span>
                            </div>
                        </div>
                    </div>
                    <div className="divider" />
                    <div className="d-flex w-100 justify-content-center">
                        <Chart options={options} series={series} type="donut" width="380" />
                    </div>
                </CardBody>
            </Card>

        </>
    );
}
