import React, {useState, useEffect,useContext} from 'react'
import clsx from 'clsx';
import { PageTitle } from '../../layout-components';
import { InsightsOverviewContext } from 'contexts/InsightsOverviewContext';
import SentimentOverview from "../../custom-components/InsightsOverview/SentimentOverview";

export default function InsightsOverview() {
    const {
        setTotalTweets,
        setCurrentSentimentOnVaccine,
        setCurrentVaccinationRate,
        setSentimentDistribution} = useContext(InsightsOverviewContext);
    useEffect(()=>{
        fetch('/api/insightsOverview').then(res=>{
            if(res.ok){
                return res.json()
            }
        }).then(data => {
            console.log(data);
            setTotalTweets(data.totalTweets);
            setCurrentSentimentOnVaccine(data.currentSentimentOnVaccine);
            setCurrentVaccinationRate(data.currentVaccinationRate);
            setSentimentDistribution(data.sentimentDistribution);
        })
    },[]);

    return (
        <>
            <PageTitle
                titleHeading="Insights Overview"
                titleDescription="High Level Statistics of overall Australia public perception on COVID-19 Vaccine"
            />
            <SentimentOverview  />
        </>
    )
}
