


import React, { useState, useContext } from 'react'
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Row, Col, Card, TabContent, TabPane, Nav, NavItem, Button } from 'reactstrap';
import { NavLink as NavLinkStrap } from 'reactstrap';
import GaugeChart from 'react-gauge-chart';
import SentimentTimeCharts from "../SentimentTimeCharts";
import SentimentDistribution from "../SentimentDistribution";
import PerceptionBreakdown from "../PerceptionBreakdown";
import { UncontrolledAlert } from 'reactstrap';

//import context
import { InsightsOverviewContext } from 'contexts/InsightsOverviewContext'

